<template>
    <div>
        <div class="title">
            热门推荐
        </div>
        <div>
            <div v-for="item of items" :key="item.id">
                <div class="div">
                    <div class="left"><img :src="item.img" alt=""></div>
                    <div class="right">
                        <p>{{item.p1}}</p>
                        <p class="p2">{{item.p2}}</p>
                        <router-link :to="item.id">
                            <button>{{item.btn}}</button>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'HomeRecommend',
    data () {
        return{
            items:[
                {
                    id:'001',
                    img:'http://img1.qunarzz.com/sight/p0/1409/19/adca619faaab0898245dc4ec482b5722.jpg_250x250_0fc722c0.jpg',
                    p1:'故宫',
                    p2:'东方建筑宫殿代表，世界建筑宫殿代表',
                    btn:'查看详情',
                },
                {
                    id:'002',
                    img:'http://img1.qunarzz.com/sight/p0/1708/2b/2b3b94de99c0a425a3.img.jpg_250x250_50c99cf3.jpg',
                    p1:'八达岭长城',
                    p2:'居庸之险不在关而在八达岭',
                    btn:'查看详情',
                },
                {
                    id:'003',
                    img:'http://img1.qunarzz.com/sight/p0/1505/d2/d274c92de14c93da.water.jpg_250x250_d852635b.jpg',
                    p1:'颐和园',
                    p2:'中国清朝时期皇家园林',
                    btn:'查看详情',
                },
                {
                    id:'004',
                    img:'http://img1.qunarzz.com/sight/p0/1505/f5/f5f45e1a83537bcb.water.jpg_250x250_63e3520a.jpg',
                    p1:'圆明园',
                    p2:'清代著名皇家园林',
                    btn:'查看详情',
                },
            ]
        }
    }
}
</script>

<style scoped>
    .title{
        font-size: 16px;
        height: 40px;
        background: #eeeeee;
        line-height: 40px;
        padding-left: 5px;
    }
    .left{
        width: 40%;
        float: left;
        padding: 5px;
        
    }
    .right{
        width: 52%;
        float: left;
        margin: 10px;
    }
    img{
        width: 100%;
        height: 180px;
        
    }
    button{
        background: #fd9226;
        padding: 10px;
        color: white;
        border: none;
    }
    .div{
        overflow: hidden;
        border-bottom: 1px solid black;
        margin-top: 10px;
    }
    .p2{
        color: #cccccc;
    }
</style>





