<template>
    <div>
        <router-link to="/">
        <img src="@/assets/left.png" alt="">
        </router-link>
        <p>城市选择</p>
    </div>
</template>

<script>
export default {
    name:"CityHeader"
}
</script>

<style scoped>
    div{
       background: #00bcd4; 
       width: 100%;
       height: 50px;
    }
    img{
        width: 20px;
        height: 20px;
        margin-top: 20px;
        float:left;
        margin-left: 5px;
    }
    p{
        float: left;
        text-align: center;
        margin:20px auto 0 auto;
        color: white;
        width: 80%;
    }

</style>





