            <span>八达岭</span>Pa-ta-ling，亦作Badaling，是峰峦叠嶂的军都山中的一个山口，位于北京西北60公里处，东经116°65’，北纬40°25’。八达岭景区以八达岭长城为主。

八达岭[1]长城是明长城中保存最好的一段，也是最具代表性的一段，是明代长城的精华，是长城重要关口居庸关的前哨，海拔高达1015米，地势险要，城关坚固。八达岭长城史称天下九塞之一，是万里长城的精华和杰出代表。高超的建筑技艺和不朽的艺术价值，充分体现了中国古代劳动人民的智慧和力量。 八达岭长城是万里长城向游人开放最早的地段。
