        <div class="introduce">
            <span>圆明园</span>又称圆明三园，是清代大型皇家园林，它坐落在北京西北郊，与颐和园毗邻，由圆明园、长春园和绮春园组成，所以也叫圆明三园。此外，还有许多小园，分布在东、西、南三面，众星拱月般环绕周围。 [1] 
占地面积3.5平方千米，建筑面积达20万平方米，一百五十余景，有“万园之园”之称。 [2]  清帝每到盛夏就来到这里避暑、听政，处理军政事务，因此也称“夏宫”。
        </div>
