        height: 40px;
        line-height: 40px;
        border: 1px solid #cccccc;
        padding: 5px;
        overflow: hidden;
    }
    .dw{
        margin-top: 5px;
        width: 30px;
        height: 30px;
        float: left;
    }
    p{
        float: left;
        padding: 0;
        margin: 0;
    }
    .ticket{
        height: 40px;
        background: #cccccc;
        line-height: 40px;
        padding: 5px;
        color: white;
        font-size: 20px;
    }
    
    .s-l{
        float: left;
    }
    .s-r{
        float: right;
        color: orange;
    }
    .s,.x{
       overflow: hidden; 
       border: 1px solid #cccccc;
       height: 40px;
       line-height: 40px;
    }
</style>



