<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" type="text" placeholder="输入城市名或拼音" />
    </div>
    <div
      class="search-content"
      ref="search"
      v-show="keyword"
    >
      <ul>
        <li
          class="search-item border-bottom"
          v-for="item of list"
          :key="item.id"
          @click="handleCityClick(item.name)"
        >
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="hasNoData">
          没有找到匹配数据
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  name: 'CitySearch',
  props:{
    cities: Object
  },

  data () {
    return {
      keyword: '',
      list: [],
      timer: null,
    }
  },
  computed: {
    hasNoData () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  methods: {
    handleCityClick (city) {
      this.$store.commit('changCity',city)
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
    .search-input{
        width: 97%;
        height: 30px;
        padding: 0 1% 0 1%;
    }
    .search-content{
        z-index: 1;
        overflow: hidden;
        position: absolute;
        top: 90px;
        left: 0;
        right: 0;
        bottom: 0;
        background: #eee;
    }
    .search-item{
      padding: 0;
      margin: 0;
      line-height: 30px;
      padding-left: 5px;
      background: #fff;
      color: #666;
      list-style: none;
    }
    ul{
        margin: 0;
        padding: 0;
    }

</style>



