<template>
    <div>
        <div class="title">
            热门城市
        </div>
        <div v-for="item of items" :key="item.id" @click="CityClick(item.btn)">
            <button>{{item.btn}}</button>
        </div>
    </div>
</template>

<script>
export default {
    name:"CityList",
    data () {
        return{
            items:[
                    {
                        id:"001",
                        btn:"北京",
                    },
                    {
                        id:"002",
                        btn:"上海",
                    },
                    {
                        id:"003",
                        btn:"广州",
                    },
                    {
                        id:"004",
                        btn:"深圳",
                    }
            ]
        }
    },
    methods:{
        CityClick(city){
            this.$store.commit('changCity',city)
            this.$router.push('/')
        }
    }
}
</script>

<style scoped>
    .title{
        padding-left: 1%;
        width: 99%;
        height: 30px;
        line-height: 30px;
        background: #eeeeee;
        
        font-size: 14px;
    }
    button{
        
        float: left;
        background: none;
        width: 20%;
        height: 40px;
        margin: 10px 2.5% 0 2.5%;
        border: #cccccc 1px solid;
    }
</style>



