<template>
  <div id="HomeHeader">
    <img class="left" src="@/assets/go.png" alt="">
    <div class="title">
      想去哪就去哪
    </div>
    <router-link to="/city">
      <span>
      {{this.$store.state.city}}
        <img class="down" src="@/assets/down.png" alt="">
      </span>
    </router-link>
  </div>
</template>


<script>
export default {
  name: 'HomeHeader',
  components: {

  },
}
</script>

<style  scoped>

#HomeHeader{
  width: 100%;
  height: 64px;
  background: #1fbcd2;
}
.left{
  width: 40px;
  height: 40px;
}
.left,.input,span{
  float: left;
  margin-left: 10px;
  padding: 10px;
} 

span{
  margin: 5px;
  color: white;
  float: right;
  font-size: 18px;
}
.ss{
  width: 30px;
  height: 30px;
}
.down{
  
  width: 10px;
  height: 10px;
}
.title{
  margin-left: 15%;
  color: white;
  text-align: center;
  font-size: 20px;
  float: left;
  line-height: 60px;
}
</style>



