import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/Home'
import City from '@/components/city/City'
import a from '@/components/detail/001'
import b from '@/components/detail/002'
import c from '@/components/detail/003'
import d from '@/components/detail/004'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/city',
      name: 'City',
      component: City,
    },
    {
      path: '/001',
      name: '001',
      component: a,
    },
    {
      path: '/002',
      name: '002',
      component: b,
    },
    {
      path: '/003',
      name: '003',
      component: c,
    },
    {
      path: '/004',
      name: '004',
      component: d,
    },
  ]
})



