import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)
let defaultCity='北京'
try{
    if(localStorage.city){
        defaultCity=localStorage.city
    }
}catch (e) {}

export default new Vuex.Store({
    state:{
        city:defaultCity
    
    },
    mutations:{
        changCity(state,city){
            state.city=city
            try{
                if(localStorage.city){
                    localStorage.city=city
                }
            }catch (e) {}
        }
    }
})


