<template>
  <div id="Swiper">
    <swiper :options="swiperOption" ref="mySwiper">
       
        <swiper-slide><img src="https://imgs.qunarzz.com/p/p66/201304/17/384810d022dd28f793835fbb.jpg_256x160_6a7ec251.jpg" alt=""></swiper-slide>
        <swiper-slide><img src="http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20193/87a224d0349d94a11e97f31aa1aba4f5.jpg_750x200_1f78af87.jpg" alt=""></swiper-slide>
        <swiper-slide><img src="http://mp-piao-admincp.qunarzz.com/mp_piao_admin_mp_piao_admin/admin/20197/a4c27b0a5fc1d9e9e620225869089b12.jpg_750x200_e4720428.jpg" alt=""></swiper-slide>

        
        <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>


<script>
export default {
  name: 'HomeSwiper',
  data () {
      return{
          swiperOption:{
          }
      }
  } 
  
}
</script>


<style  scoped>
    img{
        width: 100%;
        height: 150px;;
    }
    #swiper,.swiper-slide{
      width: 100%;
    }
</style>



