<template>
    <div>
        <div class="title">周末去哪儿</div>
        
        <div v-for="item of items" :key="item.id">
            <router-link :to="item.id">
                <div class="div">
                    <img :src="item.img" alt="">
                    <p class="p1">{{item.p1}}</p>
                    <p class="p2">{{item.p2}}</p>
                </div>
            </router-link>
        </div>
    </div>
</template>

<script>
export default {
    name:'HomeRecommend2',
    data () {
        return{
            items:[
                {
                    id:'001',
                    img:'http://img1.qunarzz.com/sight/source/1811/f3/86173f863bef61.jpg_r_640x214_52b003ac.jpg',
                    p1:'京城周末撒欢',
                    p2:'在京都过周末，不仅仅是城中游',
                },
                {
                    id:'002',
                    img:'http://img1.qunarzz.com/sight/source/1811/7e/476589267ebb41.jpg_r_640x214_bf599709.jpg',
                    p1:'京城溜娃必去',
                    p2:'德智体美劳全面发展的亲子游，这些地方该去看看',
                },
                {
                    id:'003',
                    img:'http://img1.qunarzz.com/sight/source/1505/aa/7baaf8a851d221.jpg_r_640x214_1431200f.jpg',
                    p1:'京都必打卡',
                    p2:'来北京必去的景点非这些地方莫属',
                },
                {
                    id:'004',
                    img:'http://img1.qunarzz.com/sight/source/1606/85/c6b6bad1b977e.jpg_r_640x214_5c638941.jpg',
                    p1:'漂流勇进，烧烤露营',
                    p2:'轻松水上漂，任我乐逍遥',
                }
            ]
        }
    }
}
</script>

<style scoped>
    .title{
        font-size: 16px;
        height: 40px;
        background: #eeeeee;
        line-height: 40px;
        padding-left: 5px;
    }
    .p1,.p2{
        margin-left: 5px;
    }
    .p2{
        font-size: 14px;
    }
    img{
        width: 100%;
    }
    a{
        color: black;
        text-decoration: none;
    }
</style>





